import FormRenderer from './components/Form'
import {isObj, isStr} from "./util/util";

const ctx = window

// Create namespace.
const ns = ['nocake', 'form', 'renderer']
let current = ctx

for (let i = 0; i < ns.length; i++) {
    const key = ns[i]
    if (!current[key]) current[key] = {}
    current = current[key]
}

const factory = {
    create(o) {
        let el = o.el
        if (!el) {
            throw new TypeError('[FormComposer] Form Composer requires "el" config defined.')
        }

        if (isStr(el)) {
            el = document.querySelector(el)
            if (!el) {
                throw new TypeError('[FormComposer] DOM Element provided in "el" config can not be found.')
            }
        }

        // Don't use "instanceof Element".
        if (!isObj(el) || !('nodeType' in el)) {
            throw new TypeError('[FormComposer] "el" config must be type of string or HTMLElement.')
        }

        const div = document.createElement('div')
        el.appendChild(div)

        o.el = div

        const form = new FormRenderer({
            el: div,
            propsData: o
        })

        form.destroy = function() {
            form.$destroy()
            el.innerHTML = ''
        }

        return form
    }
}

ctx.nocake.form.renderer = factory


